.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    border-radius: 6px;
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
}

.moviesName{
    position: fixed;
    bottom: 5px;
    z-index: 1;
}

.smallPoster{
    border-radius: 6px;
    cursor: pointer;
    max-height: 150px;
    margin-right: 10px;
}

.poster:hover{
    transform: scale(1.1);
}

.smallPoster:hover{
    transform: scale(2)
}